"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import Link from "next/link";
import { 
  Bus, 
  ChevronRight, 
  FileBarChart, 
  Home, 
  LogOut, 
  Package, 
  Settings, 
  Wrench, 
  Truck, 
  User 
} from "lucide-react";

export default function Dashboard() {
  const router = useRouter();
  const [isLoading, setIsLoading] = useState(true);
  const [userName, setUserName] = useState("");

  // Simple client-side auth check
  useEffect(() => {
    const isAuthenticated = localStorage.getItem("isAuthenticated");
    const storedUserName = localStorage.getItem("userName");
    
    if (!isAuthenticated) {
      router.push("/");
    } else {
      setUserName(storedUserName || "Admin");
      setIsLoading(false);
    }
  }, [router]);

  const handleLogout = () => {
    localStorage.removeItem("isAuthenticated");
    localStorage.removeItem("userName");
    router.push("/");
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-screen bg-gray-100">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary"></div>
      </div>
    );
  }

  return (
    <div className="flex h-screen bg-gray-100">
      {/* Sidebar */}
      <div className="w-64 bg-white shadow-md flex flex-col">
        <div className="p-4 border-b">
          <div className="flex items-center justify-center">
            <Image 
              src="/logo.svg" 
              alt="Royal Express" 
              width={150} 
              height={40} 
              className="mb-2" 
            />
          </div>
        </div>
        
        <div className="px-4 py-6 border-b">
          <div className="flex items-center space-x-3">
            <div className="bg-primary/10 p-2 rounded-full">
              <User size={20} className="text-primary" />
            </div>
            <div>
              <p className="text-sm font-medium">{userName}</p>
              <p className="text-xs text-gray-500">Fleet Manager</p>
            </div>
          </div>
        </div>
        
        <nav className="flex-1 p-4">
          <div className="space-y-1">
            <Link href="/dashboard" className="flex items-center w-full px-3 py-2 text-sm font-medium rounded-md bg-primary/10 text-primary">
              <Home className="mr-3 h-5 w-5" />
              Dashboard
            </Link>
            
            <button className="flex items-center w-full px-3 py-2 text-sm font-medium rounded-md text-gray-700 hover:bg-gray-100">
              <Bus className="mr-3 h-5 w-5" />
              Vehicles
            </button>
            
            <button className="flex items-center w-full px-3 py-2 text-sm font-medium rounded-md text-gray-700 hover:bg-gray-100">
              <Package className="mr-3 h-5 w-5" />
              Inventory
            </button>
            
            <Link href="/dashboard/maintenance" className="flex items-center w-full px-3 py-2 text-sm font-medium rounded-md text-gray-700 hover:bg-gray-100">
              <Wrench className="mr-3 h-5 w-5" />
              Maintenance
            </Link>
            
            <button className="flex items-center w-full px-3 py-2 text-sm font-medium rounded-md text-gray-700 hover:bg-gray-100">
              <FileBarChart className="mr-3 h-5 w-5" />
              Reports
            </button>
            
            <button className="flex items-center w-full px-3 py-2 text-sm font-medium rounded-md text-gray-700 hover:bg-gray-100">
              <Settings className="mr-3 h-5 w-5" />
              Settings
            </button>
          </div>
        </nav>
        
        <div className="p-4 border-t">
          <button 
            onClick={handleLogout}
            className="flex items-center w-full px-3 py-2 text-sm font-medium rounded-md text-red-600 hover:bg-red-50"
          >
            <LogOut className="mr-3 h-5 w-5" />
            Logout
          </button>
        </div>
      </div>
      
      {/* Main Content */}
      <div className="flex-1 overflow-auto">
        <header className="bg-white shadow-sm">
          <div className="p-4">
            <h1 className="text-2xl font-semibold text-gray-800">Dashboard</h1>
          </div>
        </header>
        
        <main className="p-6">
          <div className="mb-8">
            <h2 className="text-xl font-semibold mb-4">Welcome back, {userName}!</h2>
            <p className="text-gray-600">Here's what's happening with your bus fleet today.</p>
          </div>
          
          {/* Stats Cards */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
            <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
              <div className="flex items-center">
                <div className="bg-blue-100 p-3 rounded-full">
                  <Bus className="h-6 w-6 text-blue-600" />
                </div>
                <div className="ml-4">
                  <h3 className="text-sm font-medium text-gray-500">Total Vehicles</h3>
                  <p className="text-2xl font-semibold text-gray-800">42</p>
                </div>
              </div>
              <div className="mt-4">
                <div className="flex items-center text-sm text-green-600">
                  <span>38 active</span>
                  <span className="mx-2">•</span>
                  <span>4 in maintenance</span>
                </div>
              </div>
            </div>
            
            <Link href="/dashboard/maintenance" className="bg-white p-6 rounded-lg shadow-sm border border-gray-200 hover:border-yellow-300 transition-colors">
              <div className="flex items-center">
                <div className="bg-yellow-100 p-3 rounded-full">
                  <Wrench className="h-6 w-6 text-yellow-600" />
                </div>
                <div className="ml-4">
                  <h3 className="text-sm font-medium text-gray-500">Pending Maintenance</h3>
                  <p className="text-2xl font-semibold text-gray-800">7</p>
                </div>
              </div>
              <div className="mt-4">
                <div className="flex items-center text-sm text-yellow-600">
                  <span>3 urgent</span>
                  <span className="mx-2">•</span>
                  <span>4 scheduled</span>
                </div>
              </div>
            </Link>
            
            <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
              <div className="flex items-center">
                <div className="bg-purple-100 p-3 rounded-full">
                  <Package className="h-6 w-6 text-purple-600" />
                </div>
                <div className="ml-4">
                  <h3 className="text-sm font-medium text-gray-500">Inventory Items</h3>
                  <p className="text-2xl font-semibold text-gray-800">215</p>
                </div>
              </div>
              <div className="mt-4">
                <div className="flex items-center text-sm text-red-600">
                  <span>14 low stock</span>
                  <span className="mx-2">•</span>
                  <span>3 out of stock</span>
                </div>
              </div>
            </div>
          </div>
          
          {/* Recent Activities & Vehicles Sections */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-medium">Recent Activities</h3>
                <button className="text-sm text-primary flex items-center">
                  View all <ChevronRight className="h-4 w-4 ml-1" />
                </button>
              </div>
              
              <div className="space-y-4">
                {[
                  { icon: <Truck className="h-5 w-5 text-blue-500" />, text: "Bus #1204 completed maintenance", time: "2 hours ago" },
                  { icon: <Package className="h-5 w-5 text-green-500" />, text: "New shipment of spare parts received", time: "5 hours ago" },
                  { icon: <Wrench className="h-5 w-5 text-yellow-500" />, text: "Bus #0935 scheduled for maintenance", time: "Yesterday" },
                ].map((activity, index) => (
                  <div key={index} className="flex items-start">
                    <div className="bg-gray-100 p-2 rounded-full mr-4">
                      {activity.icon}
                    </div>
                    <div>
                      <p className="text-sm font-medium">{activity.text}</p>
                      <p className="text-xs text-gray-500">{activity.time}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
            
            <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-medium">Vehicle Status</h3>
                <button className="text-sm text-primary flex items-center">
                  View all <ChevronRight className="h-4 w-4 ml-1" />
                </button>
              </div>
              
              <div className="space-y-4">
                {[
                  { id: "RW-1204", status: "Available", statusColor: "bg-green-100 text-green-800", location: "Kigali Depot", lastService: "2 days ago" },
                  { id: "RW-0935", status: "In Maintenance", statusColor: "bg-yellow-100 text-yellow-800", location: "Service Center", lastService: "In progress" },
                  { id: "RW-0842", status: "On Route", statusColor: "bg-blue-100 text-blue-800", location: "Kigali-Butare", lastService: "1 week ago" },
                  { id: "RW-1158", status: "Available", statusColor: "bg-green-100 text-green-800", location: "Huye Depot", lastService: "3 days ago" },
                ].map((vehicle, index) => (
                  <div key={index} className="flex items-center justify-between p-3 border-b last:border-0">
                    <div className="flex items-center">
                      <Bus className="h-5 w-5 text-gray-400 mr-3" />
                      <div>
                        <p className="text-sm font-medium">Bus {vehicle.id}</p>
                        <p className="text-xs text-gray-500">{vehicle.location}</p>
                      </div>
                    </div>
                    <div className="flex items-center">
                      <span className={`text-xs px-2 py-1 rounded-full ${vehicle.statusColor}`}>
                        {vehicle.status}
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
} 